import { Modal, Button, Image, Container, Row, Col } from 'react-bootstrap';
import { useState } from 'react';
export default function ModalGeneral(props) {
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  return (
    <>
      <Button variant="primary" onClick={handleShow}>
        {props.children}
      </Button>

      <Modal
        show={show}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Info Doctor</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Container>
            <Row>
              <Col lg={5}>
                <Image width="100%" src={props.avatar} roundedCircle />
              </Col>
              <Col lg={7}>
                <h5>Tên: {props.name}</h5>
                <p>Kinh nghiệm: {props.experience}</p>
                <p>Vị trí: {props.position}</p>
                <p>Thông tin: {props.description}</p>
              </Col>
            </Row>
          </Container>

        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}