import { useState, useEffect } from "react";
import { Table, Container } from 'react-bootstrap';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useRouteMatch,
} from "react-router-dom";
import axios from "axios";
import ModalGeneral from "../general/modal";

export default function Index() {
  let match = useRouteMatch();
  const [data, setData] = useState([]);
  useEffect(() => {
    const getAPI = async () => {
      try {
        await axios.get('https://projecterm.herokuapp.com/admin').then(res => setData(res.data));
      } catch (e) {
        console.log(e);
      }
    }
    getAPI();
  }, [])
  console.log(data)
  return (
    <Container>
      <Link to={`${match.url}/createPatient`}>Add Patient</Link>
      {/* <Router>
        <Link to={`${match.url}/createPatient`}>Add Patient</Link>
        <Switch>
          <Route path={`${match.url}/createPatient`}>
            <CreatePatient />
          </Route>
        </Switch>
      </Router> */}
      <Table striped bordered hover variant="dark">
        <thead>
          <tr>
            <th>#</th>
            <th>Tên Bác Sĩ</th>
            <th>Vị Trí</th>
            <th>Số Bệnh Nhân</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          {data.map((value, index) =>
            <tr key={index}>
              <td>2</td>
              <td>{value.name}</td>
              <td>{value.position}</td>
              <td>4</td>
              <td>
                <ModalGeneral {...value}>Thông tin bác sĩ</ModalGeneral>
              </td>
            </tr>
          )}
        </tbody>
      </Table>
    </Container>
  )
}