import { Card, Container, Col, Row } from 'react-bootstrap';
import { useEffect, useState } from 'react';
import axios from 'axios';

import ModalGeneral from '../components/general/modal';
export default function Doctors() {
  const [doctor, setDoctor] = useState([]);
  useEffect(() => {
    const getAPI = async () => {
      try {
        await axios.get('https://projecterm.herokuapp.com/doctors').then(res => setDoctor(res.data));
      } catch (e) {
        console.log(e);
      }
    }
    getAPI();
  }, [])
  return (
    <Container>
      <Row>
        {doctor.map((value, index) =>
          <Col lg={3} key={index}>
            <Card>
              <Card.Img variant="top" src={value.avatar} />
              <Card.Body>
                <Card.Title>{value.name}</Card.Title>
                <Card.Text>
                  {value.description}
                </Card.Text>
                <ModalGeneral {...value}>Detail</ModalGeneral>
              </Card.Body>
            </Card>
          </Col>
        )}
      </Row>
    </Container>
  )
}